  position: fixed;
  width: 100%;
  height: 100%;
  display: none;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  background: rgba(122, 122, 122, 0.5);
}
.md-show ~ .md-overlay {
  opacity: 1;
  display: block;
}
