    -webkit-transform: translate(0, 2px);
    transform: translate(0, 2px);
    -webkit-transition: opacity 0 ease-in;
    transition: opacity 0 ease-in;
    -webkit-transition: opacity 0 ease-in, -webkit-transform 0 ease-in;
    transition: opacity 0 ease-in, -webkit-transform 0 ease-in;
    transition: transform 0 ease-in, opacity 0 ease-in;
